import { ArrowRight, ChevronUp, Info, ShoppingBag, Trash2, X } from 'lucide-react';
import React, { useCallback, useEffect, useMemo, useRef, useState } from 'react';
import * as THREE from 'three';

import "../../styles/globals.css";

interface Product {
  id: string;
  name: string;
  price: number;
  description: string;
  color: string;
  weightGrams: number; 
}

interface DynamicProduct extends Product {
    uniqueKey: string;
}

interface FlyoutTarget { 
    endX: number;
    endY: number;
}

interface GroupedCartItem {
    product: Product;
    quantity: number;
}
type CartState = Record<string, GroupedCartItem>;

